.login-wrap{
    width: 100%;
   margin : 25px 0px 10px 500px;
    max-width: 450px;
    min-height: 650px;
    position: relative;
    border: 2px solid #C3073F;
   opacity: 1;
 
}
.login-html{
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 35px 40px 0px 40px;
    background: #1A1A1D;
    
    
}
.login-html .sign-in-html, .login-html .sign-up-html{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    transform: rotateY(180deg);
    backface-visibility: hidden;
    transition: all .4s linear;
}
.login-html .sign-in, .login-html .sign-up, .login-form .group .check{
    display: none;
}
.login-html .tab, .login-form .group .label, .login-form .group .button{
    text-transform: uppercase;
}
.login-html .tab{
    font-size: 22px;
    margin-right: 15px;
    padding-bottom: 5px;
    margin: 0px 0px 0px 10px;
    display: inline-block;
    color: #45A29E;
    border-bottom: 2px solid transparent;
}
.login-html .sign-in:checked + .tab, .login-html .sign-up:checked +.tab{
    color: #C3073F;
    border-color: #fff;
}
.login-form{
    min-height: 345px;
    position: relative;
    perspective: 1000px;
    transform-style: preserve-3d;
    margin-top: 15px;
}
.login-form .group{
    margin-bottom: 15px;
}
.login-form .group .label,
.login-form .group .input,
.login-form .group .button{
    width: 100%;
    color: #fff;
    display: block;
}
.login-form .group .input,
.login-form .group .button{
    border: none;
    padding: 15px 20px;
    border-radius: 25px;
    background: #4E4E50;
}
.login-form .group input[data-type="password"]{
    
    -webkit-text-security: circle;
}
.login-form .group .label{
    color: #45A29E;
    font-size: 12px;
}
.login-form .group .button{
    background: #C3073F;
}
.login-form .group .button :hover{
    background: #45A29E;
}
.login-form .group .label .icon{
    width: 15px;
    height: 15px;
    border-radius: 2px;
    position: relative;
    display: inline-block;
    background: rgba(255,255,255,.1);
}
.login-form .group label .icon:before,
.login-form .group label .icon:after{
    content: '';
    width: 10px;
    height: 2px;
    background: #fff;
    position: absolute;
    transition: all .2s ease-in-out 0s;
}
.login-form .group label .icon:before{
    left: 3px;
    width: 5px;
    bottom: 6px;
    transform: scale(0) rotate(0);
}
.login-form .group label .icon:after{
    top:6px;
	right:0;
	transform:scale(0) rotate(0);
}
.login-form .group .check:checked + label{
	color:#C3073F;
}

.login-form .group .check:checked + label .icon:before{
	transform:scale(1) rotate(45deg);
}
.login-form .group .check:checked + label .icon:after{
	transform:scale(1) rotate(-45deg);
}
.login-html .sign-in:checked + .tab + .sign-up + .tab + .login-form .sign-in-html{
	transform:rotate(0);
}
.login-html .sign-up:checked + .tab + .login-form .sign-up-html{
	transform:rotate(0);
}

